body {
  margin: 0;
  padding: 0;
  background-color: #e9e9e9;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.List-container {
  margin: 1em 5em;
}

.List-header {
  color: #737373;
}

.List-footer {
  display: flex;
  overflow: auto;
  margin-top: 20px;
  min-height: 300px;
  flex-wrap: nowrap;
}

.List-details {
  flex: 0 0 auto;
  max-width: 350px;
  background-color: #fff;
  margin: 0px 20px 20px 0px;
}

.List-name {
  width: 100px;
  margin: 20px 0px;
  line-height: 18px;
  padding: 10px 0px 10px 10px;
}

.List-delete {
  color: #000;
  margin: 20px;
  padding: 10px;
  max-width: 200px;
  position: relative;
  background-color: #fff;
  border: 1px solid #d6d6d6;
}

.List-new-card {
  color: #000;
  width: 100%;
  padding: 10px;
  display: block;
  margin: 20px 0px;
  position: relative;
  background-color: #fff;
}

.List-add-detais {
  flex: 0 0 auto;
}

.List-add {
  width: 200px;
  padding: 10px;
  color: #000;
  position: relative;
  background-color: #fff;
  margin: 0px 20px 20px 0px;
  border: 1px solid #d6d6d6;
}

.ListCard-label {
  width: 90%;
  margin: 10px;
  color: #000;
  height: 100px;
  padding: 10px;
  display: block;
  border: 1px solid #d6d6d6;
}

.Comments-container {
  float: left;
  width: 100%;
  padding: 20px;
  display: block;
  background-color: #fff;
}

.Comments-textarea {
  width: 80%;
  padding: 11px;
  outline: none;
  display: block;
  font-size: 13px;
  min-height: 100px;
  border: 1px solid #d6d6d6;
}

.Comments-add {
  padding: 10px;
  max-width: 20%;
  display: block;
  cursor: pointer;
  margin: 20px 0px;
  overflow: hidden;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  border: 1px solid #d6d6d6;
}

.Comments-history {
  width: 50%;
  float: left;
}

.Comments-history-child {
  float: left;
  width: 100%;
  margin-bottom: 20px;
}

.Comments-history-desc {
  float: left;
  max-width: 60%;
}

.Comments-history-time {
  float: right;
  max-width: 30%;
}

.Card-container {
  margin: 1em 5em;
}

.Card-header {
  color: #737373;
}

.Card-footer {
  margin-top: 20px;
  min-height: 300px;
}

.Card-details {
  width: 100%;
  padding: 20px;
  display: block;
  background-color: #fff;
}

.Card-name {
  width: 100%;
  display: block;
  margin-bottom: 15px;
}

.Card-desc {
  width: 80%;
  padding: 10px;
  display: block;
  min-height: 100px;
  border: 1px solid #d6d6d6;
}

.Card-delete {
  padding: 10px;
  max-width: 20%;
  display: block;
  border: 1px solid #d6d6d6;
}

.Board-parent {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.Board-container {
  width: 100%;
  max-width: 450px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.25) !important;
}

.Board-main {
  width: 100%;
  height: auto;
  padding: 50px 90px 50px 90px;
}

.Board-label {
  font-size: 16px;
  font-weight: bold;
  line-height: 19px;
  margin-bottom: 15px;
}

.Board-input {
  width: 100%;
  padding: 11px;
  outline: none;
  color: #000000;
  font-size: 13px;
  border: 1px solid #d6d6d6;
}

.Board-textarea {
  width: 100%;
  outline: none;
  padding: 11px;
  font-size: 13px;
  margin-top: 15px;
  min-height: 60px;
  border: 1px solid #d6d6d6;
}

.Board-button {
  outline: none;
  color: #fff;
  font-size: 16px;
  margin-top: 15px;
  padding: 8px 25px;
  border-radius: 3px;
  background-color: #f0483e;
}

* {
  box-sizing: border-box;
}

a {
  color: #000;
  text-decoration: none;
}

.fleft {
  float: left;
}

.fright {
  float: right;
}

.tcenter {
  text-align: center;
}

.cpointer {
  cursor: pointer;
}

.w100 {
  width: 100%;
}

.text-overflow {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -ms-text-overflow: ellipsis;
}

.App-header {
  width: 100%;
  z-index: 9999;
  min-height: 4em;
  position: fixed;
  padding: 1em 5em;
  background-color: #fff;
}

.App-icon {
  width: 15px;
  height: 15px;
  background-color: #868686;
  margin: 8.5px 15px 8.5px 0px;
}

.App-name {
  color: #737373;
  line-height: 2em;
  font-weight: bold;
}

.App-container {
  top: 5em;
  width: 100%;
  position: relative;
}

